
import { defineComponent, PropType, toRefs} from 'vue'

export type TListItem = {
  title: string
  href?: string
  body: string[]
}

export default defineComponent({
  name: "VList",
  props: {
    items: {
      type: Object as PropType<TListItem[]>,
      required: true
    }
  },
  setup(props) {
    const { items } = toRefs<{items: TListItem[]}>(props)
    return { items }
  }
})
